.dashboard {
    display: flex;
    height: 100vh;
}

.side-bar {
    width: 300px;
    height: 100vh;
    background-color: var(--primaryColor1);
    box-shadow: var(--shadow);
}

.dash-body {
    background-color: #ECF0F1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.dash-top-meu {
    width: 100%;
    height: 12vh;
    background-color: white;
    position: absolute;
    top: 0;
    box-shadow: var(--shadow);
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.left-menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    margin-left: 35px;
}
.avatar{
    width: 50px;
    height: 50px;
    border-radius: 50px;
    /* background-color: red; */
    box-shadow: var(--shadow2);
    margin-right: 10px;
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    font-weight: 100px;
    color: var(--primaryColor1);
}
.left-menu>span{
    display: block;
    color: black;
    opacity: 0.75;
    font-size: 1rem;
}
.right-menu{
    display: flex;
}
.notif{
    position: relative;
    width: 50px;
    height: 50px;
    border: 1px solid var(--primaryColor3);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 35px;
    margin-right: 30px;
    color: var(--primaryColor1);
}
.notif>span{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10px;
    height: 10px;
    background-color: var(--primaryColor1);
    border-radius: 10px;
    position: absolute;
    top: 5px;
    right: 2px;
    color: white;
}
.dicon{
    position: relative;
    width: 50px;
    height: 50px;
    border: 1px solid #C0392B;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 35px;
    margin-right: 30px;
    color: #C0392B;
    cursor: pointer;
}

.dash-contents {
    width: 90%;
    height: 80vh;
    position: absolute;
    top: 120px;
    overflow: auto;
    padding: 20px;
    background-color: white;
    box-shadow: var(--shadow2); 
    border-radius: 20px;
}
