/* make variables */
:root {
  --primaryColor1: #21618C;
  --primaryColor2: #2471A3;
  --primaryColor3: #93c6e3;
  --lightGreenCol: #76D7C4;
  --darkGreenCol: #117A65;
  --darkRedCol: #7B241C;
  --lightRedCol: #E6B0AA;
  --withCol: white;
  --shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  --shadow2: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
  --tabHeadColor: #808B96;
}

* {
  font-family: 'Lato', sans-serif;
}

.medium-icons-blue {
  font-size: 35px;
  color: var(--primaryColor1);
}

.medium-icons-white {
  font-size: 35px;
  color: var(--withCol);
}

.small-icons {
  font-size: 20px;
}

.small-ml {
  margin-left: 20px;
}
.small-mt {
  margin-top: 10px;
}

.small-mr {
  margin-right: 10px;
}

.page-title {
  opacity: 0.75;
  font-size: 1rem;
  color: var(--tabHeadColor);
  margin-bottom: 2rem;
  width: 100%;
  padding-bottom: 1rem;
  border-bottom: 0.5px solid var(--tabHeadColor);
  text-transform: uppercase;
  display: flex;
  justify-content: left;
  align-items: center;
  gap: 1rem;
}

.page-sub-title {
  opacity: 0.75;
  font-size: 0.6rem;
  color: var(--tabHeadColor);
  /* margin-bottom: 2rem; */
  width: 100%;
  padding-bottom: 0.5rem;
  border-bottom: 0.5px solid var(--primaryColor3);
  text-transform: uppercase;
}

.page-sub-sub-title {
  opacity: 0.75;
  font-size: 0.4rem;
  color: var(--tabHeadColor);
  /* margin-bottom: 2rem; */
  width: 100%;
  padding-bottom: 0.5rem;
  text-transform: uppercase;
  display: flex;
  justify-content: end;
}

.dash-pages {
  display: flex;
  flex-direction: column;
}

.outline-icon-bnt {
  /* position: relative; */
  padding: 5px 5px;
  border: 1px solid var(--primaryColor1);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: var(--primaryColor1);
  cursor: pointer;
}

.outline-icon-bnt-red {
  border: 1px solid var(--darkRedCol);
  color: var(--darkRedCol);
}


.list-days {
  display: grid;
  grid-template-columns: repeat(auto-fit, 90px);
  gap: 1rem;
  justify-content: left;
}

.item-day {
  /* padding: 5px 30px; */
  width: 90px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F4F6F6;
  border-radius: 10px;
  color: var(--primaryColor1);
  cursor: pointer;
  font-size: 0.7rem;
}

.item-day-active {
  background-color: var(--darkGreenCol);
  color: var(--withCol);
}

.list-hours, .list-srv {
  display: grid;
  grid-template-columns: repeat(auto-fit, 170px);
  gap: 1rem;
  justify-content: left;
}
.list-srv{
  height: 120px;
  grid-template-columns: repeat(auto-fit, 120px);
  padding: 0px 10px;
  gap: 1.5rem;
  overflow: auto;
}

.item-hour {
  width: 170px;
  height: 65px;
  border: 1px solid #F8F9F9;
  border-radius: 20px;
  box-shadow: var(--shadow2);
  background-color: #F4F6F6;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.item-hour-indic {
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background-color: var(--tabHeadColor);
}

.item-hour-active {
  background-color: var(--lightGreenCol);

}

.item-hour-value {
  font-size: 1rem;
  color: var(--primaryColor1);
}

.item-hour-indic-active {
  background-color: var(--darkGreenCol);
}

.item-hour-value-active {
  color: var(--withCol);
}

.status-service-green {
  background-color: var(--lightGreenCol);
  border-radius: 20px;
  color: var(--darkGreenCol);
  font-size: 0.8rem;
  font-weight: bold;
  padding: 5px 20px;
  text-transform: uppercase;
}
.status-service-red {
  background-color: var(--lightRedCol);
  border-radius: 20px;
  color: var(--darkRedCol);
  font-size: 0.8rem;
  font-weight: bold;
  padding: 5px 20px;
  text-transform: uppercase;
}