.input__container {
    width: 90%;
    height: 40px;
    border: 1px solid var(--tabHeadColor);
    border-radius: 10px;
    padding: 8px 15px;
    margin: 5px 0px;
    margin-right: 20px;
    justify-content: flex-start;
    display: flex;
    align-items: center;
}
.input__container svg {
    color: var(--primaryColor1);
}

.input__container input {
    width: 100%;
    margin-left: 5px;
}

.input__container input {
    border: none;
}

.input__container input:focus {
    outline: none;
}

.input__error {
    font-size: 12px;
    padding: 3px;
    color: red;
    display: block;
}

