.signup {
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.signup__left {
    flex: 1;
    padding: 0px 50px;
}

.signup__right {
    color: white;
    background-color: var(--primaryColor1);
    padding: 0 40px;
    height: 100%;
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.input-group{
    display: flex;
    justify-content: center;
    gap: 3rem;
}