.button {
    border: none;
    height: 40px;
    width: 150px;
    padding: 5px 20px;
    font-size: 20px;
    border-radius: 10px;
    background-color: var(--primaryColor3);
    cursor: pointer;
    margin-bottom: 30px;
}

.button-outline {
    margin-top: 10px;
    border: 1px solid white;
    height: 30px;
    border-radius: 10px;
    padding: 20px 30px;
    background: none;
    color: white;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.step-button-outline {
    margin-top: 10px;
    border: 1px solid var(--primaryColor1)!important;
    height: 30px;
    border-radius: 10px;
    padding: 20px 30px !important;
    background: none;
    color: var(--primaryColor1)!important;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.button-outline:hover{
    background-color: var(--withCol);
    color: var(--primaryColor1);
}
a {
    text-decoration: none;
    font-size: 1rem;
    color: var(--primaryColor1);
}
.bg-button{
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--primaryColor1);
    color: white;
    border: none;
    border-radius: 10px;
    font-weight: bold;
    box-shadow: var(--shadow2);
    cursor: pointer;
    padding: 15px 30px;
}