.sidebar {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
   
    
}
.sidebar-header{
    display: flex;
    width: 100%;
    height: 20vh;
    justify-content: center;
    align-items: center;
    border-bottom: 0.2px solid #F8F9F9;
    position: relative;
}
.sidebar-header>img{
    width: 200px;
}
.sidebar-header>span{
    display: block;
    color: white;
    font-size: 1.7rem;
    position: absolute;
}
.sidebar-header>:nth-of-type(1){
    left: 10px;
}
.sidebar-header>:nth-of-type(2){
    right: 5px;
}
ul{
    width: 100%;
    list-style: none;
    margin-top: 1rem;
}
li{
    width: 100%;
    height: 60px; 
    display: flex;
    justify-content: start;
    align-items: center;
    font-size: 1rem;
    padding-left: 10px;
    /* padding-right: 10px; */
    /* cursor: pointer; */
}
.link{
    color: #D7DBDD;
    display: block;
    justify-content: center;
    align-items: center;
    font-family: 'Lato', sans-serif;

}
.sidebar-body{
    margin-left: 10px;
    padding-right: 20px;
}
.active{
    background-color: var(--primaryColor2);
    border-radius: 10px;
}
