.signin {
    height: 100vh;
    display: flex;
    /* justify-content: center;
    align-items: center; */
}

.signin__left {
    flex: 1;
    padding: 0px 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.signin__right {
    color: white;
    background-color: var(--primaryColor1);
    padding: 0 40px;
    height: 100%;
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}