.dmnform {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
    grid-auto-rows: minmax(100px, auto);
    justify-content: center;
    width: 100%;
}

.dmn-form-c {
    margin-top: 2rem;
}

.rdv {
    display: flex;
    gap: 2rem;
}

.list-hour {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(auto-fit, 150px);
    gap: 1rem;
    justify-content: left;
}

